.project-wrapper {
    background-color: black;
}

.project-padding {
    padding-top: 100px;
}

.card {
    height: 510px;
}

.ct-ct {
    min-height: 180px;
}

.card-title {
    font: normal normal normal 30px/37px Poppins SemiBold;
    color: rgba(255, 255, 255, 0.92);
    text-shadow: 0 0 10px rgb(0, 0, 0);
    text-align: center;
    /* height: 75px; */
}

.card-text {
    font: normal normal normal 15px/22px Poppins Medium;
    color: rgba(255, 255, 255, 0.92);
    text-shadow: 0 0 10px rgb(0, 0, 0);
    text-align: center;
}

.card-button-container {
    text-align: center;
}

.card-button {
    margin-left: 3px;
    margin-right: 3px;
    margin-top: 7px;
    font: normal normal normal 15px/22px Poppins Light;
}

.card-button:hover {
    transition: all 0.2s ease-in;
    color: rgba(255, 255, 255, 0.92);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.15);
    border-color: rgba(255, 255, 255, 0.92);
    background-color: rgba(255, 255, 255, 0.1);
}