.sttButton {
    position: fixed;
    display: flex;
    margin: 0;
}

.bi-arrow-up-square {
  position: fixed;
  right: 32px;
  bottom: 32px;
  font-size: 3.5rem;
  z-index: 1;
  cursor: pointer;
  color: white;
  
}

.vfadein {
  animation-duration: 1.5s;
  animation-name: animateButtonFadeIn;
  animation-iteration-count: 1;
  animation-direction: alternate;
}

@keyframes animateButtonFadeIn {
  from {
    display: inline;
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.vfadeout {
  animation-duration: 2s;
  animation-name: animateButtonFadeOut;
  animation-iteration-count: 1;
  animation-direction: alternate;
}

@keyframes animateButtonFadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    display: none;
  }
}
