.contact-wrapper {
  background-color: black;
}

.contact-link {
  font-family: Poppins-Bold;
  text-decoration: none;
  color: white;
}

.contact-link:hover {
  color: rgb(119, 162, 208);
}

.email, .phone {
  font-family: Roboto-Medium;
  color: white;
  letter-spacing: 0.3em;
  margin-top: 1px;
}

@media only screen and (min-width: 1441px) and (max-width: 2159px) {
  .contact-link {
    font-size: 100px;
  }

  .email, .phone {
    font-size: 15px;
  }
}

@media only screen and (min-width: 1311px) and (max-width: 1440px) {
  .contact-link {
    font-size: 90px;
  }

  .email, .phone {
    font-size: 14px;
  }
}

@media only screen and (min-width: 1001px) and (max-width: 1310px) {
  .contact-link {
    font-size: 80px;
  }

  .email, .phone {
    font-size: 13px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1000px) {
  .contact-link {
    font-size: 70px;
  }

  .email, .phone {
    font-size: 12px;
  }
}

@media only screen and (min-width: 580px) and (max-width: 767px) {
  .contact-link {
    font-size: 60px;
  }

  .email, .phone {
    font-size: 12px;
  }
}

@media only screen and (min-width: 415px) and (max-width: 579px) {
  .contact-link {
    font-size: 50px;
  }

  .email, .phone {
    font-size: 12px;
  }
}

@media only screen and (max-width: 414px) {
  .contact-link {
    font-size: 40px;
  }

  .email, .phone {
    font-size: 12px;
  }
}